.default {
    text-decoration: none;
    display: inline-block;
    width: 140px;
    height: 45px;
    line-height: 45px;
    border-radius: 45px;
    margin: 10px 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #524f4e;
    background: white;
    box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
    transition: .3s;
    border: none;
}
.default:hover {
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46, 229, 157, .4);
    color: white;
    transform: translateY(-7px);
}

.default:disabled {
    transform: none;
    opacity: 0.5;
    background: white;
    transition: none;
    color: #524f4e;
    box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
}

.red {
    color: #524f4e;
    background: white;
}

.red:hover {
    background-color: #ff7070;
    box-shadow: 0 15px 20px rgba(220, 76, 76, 0.4);
}

